<template>
  <v-flex class="py-8 px-8">
    <v-snackbar top>hiii</v-snackbar>
    <v-card class="mx-auto">
      <v-card-text class="pt-0">
        <v-row align="start">
          <v-col cols="12" sm="6">
            <v-card-title class="text-h5 justify-center"
              >Probe Initiate Form</v-card-title
            >
            <p class="text-center text-decoration-underline">
              Input the following fields to initiate a probe
            </p>
            <v-select
              :items="countryOptions"
              outlined
              label="Country"
              v-model="selectedCountry"
              required
            ></v-select>
            <v-select
              :items="languageOptions"
              outlined
              label="Language"
              v-model="selectedLanguages"
              multiple
              ><template v-slot:selection="{ item }">
                <v-chip>
                  <span>{{ item }}</span>
                </v-chip>
              </template>
            </v-select>
          </v-col>
          <v-col cols="12" sm="6" class="text-center">
            <h6>Each Languages TopK Percentages</h6>
            <v-container
              v-for="(language, index) in selectedLanguages"
              :key="language"
              class
            >
              <v-slider
                v-model="languageTopKPercentages[index]"
                :label="language"
                step="10"
                thumb-label="always"
                ticks
              ></v-slider>
            </v-container>
            <v-divider></v-divider>
            <v-row justify="space-around">
              <v-col>
                <v-btn @click="clearSelection">Clear</v-btn>
              </v-col>
              <v-col>
                <v-btn
                  @click="callInitiateProbe"
                  :loading="initiateLoading"
                  :disabled="!canInitiate"
                  >Initiate</v-btn
                >
              </v-col>
            </v-row>
          </v-col>
        </v-row>
        <!-- <v-row justify="center" class="mb-3">
          <v-btn
            @click="callInitiateProbe"
            :loading="initiateLoading"
            :disabled="initiateLoading"
            >Initiate</v-btn
          >
        </v-row> -->
      </v-card-text>
    </v-card>
    <v-card class="mt-5">
      <v-card-title class="text-h5 green--text font-weight-bold"
        >Probe Status</v-card-title
      >
      <v-simple-table>
        <template v-slot:default>
          <thead>
            <tr>
              <th class="text-left">Probe Id</th>
              <th class="text-left">Created Time</th>
              <th class="text-left">Server ID</th>
              <th class="text-left">Country</th>
              <th class="text-left">Language</th>
              <th class="text-left">State</th>
              <th class="text-left">Options</th>
            </tr>
          </thead>
          <div v-if="listLoading === true">
            <v-progress-circular
              indeterminate
              color="amber"
            ></v-progress-circular>
          </div>
          <tbody v-else>
            <tr v-for="(probe, index) in probeList" :key="probe.id">
              <td>{{ probe.id }}</td>
              <td>{{ probe.createdOn }}</td>
              <td>{{ probe.serverId }}</td>
              <td>{{ probe.country }}</td>
              <td>
                {{ probe.languages.join(",") }}({{
                  probe.percentages.join(",")
                }})
              </td>
              <td>{{ probe.state }}</td>
              <td>
                <v-dialog
                  v-model="probeDetailDialog"
                  scrollable
                  max-width="600px"
                >
                  <template v-slot:activator="{ on, attrs }">
                    <v-btn
                      color="green"
                      dark
                      small
                      @click="fetchProbeStatus(probe.id)"
                      class="mr-1"
                      v-bind="attrs"
                      v-on="on"
                    >
                      details
                    </v-btn>
                  </template>
                  <v-card>
                    <v-card-title>Probe {{ probe.id }}</v-card-title>
                    <v-divider></v-divider>
                    <v-card-text style="height: 300px">
                      <v-list dense>
                        <v-list-item-content>
                          <v-list-item-title>Request Count:</v-list-item-title>
                          {{ probeDetails.requestCount }}
                        </v-list-item-content>
                        <v-list-item-content>
                          <v-list-item-title>Live Probes({{ probeDetails.liveProbes.length }}):</v-list-item-title>
                          {{ probeDetails.liveProbes }}
                        </v-list-item-content>
                        <v-list-item-content>
                          <v-list-item-title>Latest Log:</v-list-item-title>
                          <v-list>
                            <v-list-item
                              v-for="(item, i) in probeDetails.latestLogs"
                              :key="i"
                            >
                              <v-list-item-content>
                                <v-list-item-title class="blue--text"
                                  v-text="item"
                                ></v-list-item-title>
                              </v-list-item-content>
                            </v-list-item>
                          </v-list>
                        </v-list-item-content>
                      </v-list>
                    </v-card-text>
                    <v-divider></v-divider>
                    <v-card-actions>
                      <v-btn
                        text
                        @click="probeDetailDialog = false"
                      >
                        Close
                      </v-btn>
                    </v-card-actions>
                  </v-card>
                </v-dialog>
                <v-btn
                  color="red"
                  dark
                  small
                  @click="callCloseProbe(probe.id, index)"
                  :loading="probe.closeLoading"
                  >close</v-btn
                >
              </td>
            </tr>
          </tbody>
        </template>
      </v-simple-table>
      <v-expansion-panels dark>
        <v-expansion-panel>
          <v-expansion-panel-header color="grey"
            >Probe History</v-expansion-panel-header
          >
          <v-expansion-panel-content
            ><v-simple-table>
              <template v-slot:default>
                <thead>
                  <tr>
                    <th class="text-left">Probe Id</th>
                    <th class="text-left">Created Time</th>
                    <th class="text-left">Server ID</th>
                    <th class="text-left">Country</th>
                    <th class="text-left">Languages</th>
                    <th class="text-left">Exit Status</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-for="probe in probeHistory" :key="probe.id">
                    <td>{{ probe.id }}</td>
                    <td>{{ probe.createdOn }}</td>
                    <td>{{ probe.serverId }}</td>
                    <td>{{ probe.country }}</td>
                    <td>{{ probe.languages.join(",") }}</td>
                    <td>{{ probe.exitStatus }}</td>
                  </tr>
                </tbody>
              </template>
            </v-simple-table></v-expansion-panel-content
          >
        </v-expansion-panel>
      </v-expansion-panels>
    </v-card>
  </v-flex>
</template>
<script>
import {
  getProbeList,
  getProbeStatus,
  initiateProbe,
  closeProbe,
} from "@/api/probe.js";
const ERRORSTATUS = {
  1: "Manual Stopped",
  2: "Probing Container Startup Error",
  3: "Network Error",
  4: "Probing Container Close Error",
};
const Languages = {
  "Traditional Chinese": "zh",
  English: "en",
  Spanish: "es",
  Korean: "ko",
  Russian: "ru",
  Japanese: "ja",
  French: "fr",
  Turkish: "tr",
};
const Countries = {
  Taiwan: "Taiwan",
  Albania: "Albania",
  Argentina: "Argentina",
  Australia: "Australia",
  Austria: "Austria",
  Belgium: "Belgium",
  Brazil: "Brazil",
  Bulgaria: "Bulgaria",
  Canada: "Canada",
  France: "France",
  Georgia: "Georgia",
  Germany: "Germany",
  Greece: "Greece",
  "Hong Kong": "Hong Kong",
  Iceland: "Iceland",
  Japan: "Japan",
  Mexico: "Mexico",
  "South Korea": "South Korea",
  Swedan: "Swedan",
  Singapore: "Singapore",
  "United Kingdom": "United Kingdom",
  "United States": "United States",
  Turkey: "Turkey",
};
export default {
  data() {
    return {
      countryOptions: Object.keys(Countries),
      languageOptions: Object.keys(Languages),
      selectedCountry: "",
      selectedLanguages: [],
      probeList: [],
      probeHistory: [],
      probeDetailDialog: false,
      probeDetails: {liveProbes:[]},
      listLoading: false,
      initiateLoading: false,
    };
  },
  created() {
    this.fetchProbesInfo();
  },
  methods: {
    fetchProbesInfo() {
      const vm = this;
      vm.listLoading = true;
      getProbeList().then((response) => {
        if (response.status.aliveContainers) {
          vm.probeList = response.status.aliveContainers.map((probe) => {
            return { ...probe, closeLoading: false };
          });
          vm.probeHistory = Object.values(response.history).map((probe) => {
            const { exitStatus, ...probeNoExistStatus } = probe;
            console.log(exitStatus);
            return {
              ...probeNoExistStatus,
              exitStatus: ERRORSTATUS[probe.exitStatus],
            };
          });
        }
      });
      vm.listLoading = false;
    },
    fetchProbeStatus(id) {
      const vm = this;
      getProbeStatus(id).then((response) => {
        if (!response.status) {
          vm.$bus.$emit("message:push", response.error, "error");
        }
        vm.probeDetails = response.details;
      });
    },
    callInitiateProbe() {
      const vm = this;
      vm.initiateLoading = true;
      initiateProbe(
        Countries[vm.selectedCountry],
        vm.selectedLanguages.map((language) => Languages[language]),
        vm.languageTopKPercentages
      )
        .then((response) => {
          vm.initiateLoading = false;
          if (response.status) {
            vm.$bus.$emit("message:push", "Initiate Success", "success");
          } else {
            vm.$bus.$emit("message:push", response.error, "error");
          }
        })
        .then(() => {
          vm.fetchProbesInfo();
        });
    },
    callCloseProbe(id, index) {
      const vm = this;
      vm.probeList[index].closeLoading = true;
      closeProbe(id)
        .then((response) => {
          vm.probeList[index].closeLoading = false;
          if (response.status) {
            vm.$bus.$emit("message:push", "Close Success", "success");
          } else {
            vm.$bus.$emit("message:push", response.error, "error");
          }
        })
        .then(() => {
          vm.fetchProbesInfo();
        });
    },
    clearSelection() {
      this.selectedCountry = "";
      this.selectedLanguages = [];
    },
  },
  computed: {
    canInitiate: function () {
      return (
        this.selectedCountry !== "" &&
        this.selectedLanguages.length > 0 &&
        this.initiateLoading == false
      );
    },
    languageTopKPercentages: function () {
      return this.selectedLanguages.map(() => 80);
    },
  },
};
</script>
<style lang="scss">
</style>