import request from '@/utils/request'
export function initiateProbe(country, languages, percentages) {
  return request({
    url: '/nslab/crawler/start',
    method: 'post',
    data: {
      country: country,
      languages: languages,
      percentages: percentages
    }
  })
}

export function getProbeStatus(id) {
  return request({
    url: `/nslab/crawler/${id}/status`,
    method: 'get',
  })
}
export function closeProbe(id) {
  return request({
    url: `/nslab/crawler/close/${id}`,
    method: 'delete',
  });
}

export function getProbeList() {
  return request({
    url: '/nslab/crawler/status',
    method: 'get',
  })
}

